import { Component, ViewEncapsulation } from '@angular/core';
import { SheetsService } from '../../sheets.service';
import { CommonModule } from '@angular/common';
import { BlockContentComponent, direction } from '../../templates/block-content/block-content.component';

@Component({
  selector: 'app-list-items',
  standalone: true,
  imports: [CommonModule, BlockContentComponent],
  templateUrl: './list-items.component.html',
  styleUrl: './list-items.component.less',
})
export class ListItemsComponent {
  constructor(private sheets: SheetsService) {}

  protected readonly dir = direction;
  list: any[] = [];
  url =
    'https://docs.google.com/spreadsheets/d/e/2PACX-1vQ80MI92VxqcFPGzXTkpILslN_AOIVHHzvzmafhnSg9YS98fSXvFvPwxf27sGr2148Hf0SJXF4jQd_B/pub?gid=620393183&single=true&output=tsv';

  ngOnInit() {
    const name = '';
    this.sheets.eventLoaded.subscribe(x => {
      if (x.name == name) {
        x.data.forEach((res: any) => {
          this.list.push({
            kategori: res.kategori,
            nama: res.nama,
            url: res.url,
            harga: res.harga,
            deskripsi: res.deskripsi,
          });
        });
      }
    });

    this.sheets.load(name, this.url);
  }

  grouped() {
    const type = {
      kategori: '',
      values: [{
        no: '',
        url: '',
        kategori: '',
        nama: '',
        harga: 0,
        deskripsi: '',
      }],
    };
    let arr: typeof type[] = [];

    for (const row of this.list) {
      let item = arr.find(x => x.kategori == row.kategori);

      if (item == undefined) {
        arr.push({
          kategori: row.kategori,
          values: []
        });

        item = arr.find((x) => x.kategori == row.kategori);
      }

      item?.values.push(row);
    }

    return arr;
  }

  protected tracker(i: number, x: any) {
    return x;
  }
}
